.arrowGesture {
	overflow: hidden;
	position: absolute;
	transform-origin: left;
	width: 70px;
	z-index: -1;
	display: flex;
	align-items: center;
}

.arrowPath {
	height: 3px;
	border-bottom: 4px dashed rgb(104, 238, 255);
}

.anchor,
.ship {
	color: rgb(104, 238, 255);
	transform: rotate(-90deg);
	font-size: 24px;
}
