* {
	box-sizing: border-box;
}
.notify-template-wrapper {
	margin: 0 10px;
}
.notify-template-wrapper + .notify-template-wrapper {
	margin-top: 10px;
}
[id*='top'] > .notify-template-wrapper:first-child {
	margin-top: 10px;
}
[id*='bottom'] > .notify-template-wrapper:last-child {
	margin-bottom: 10px;
}
.notify-template-wrapper > * {
	margin: 0 !important;
}
.fade-notify-in {
	animation: fade-notify-in 500ms ease-in;
}

.fade-notify-out {
	animation: fade-notify-out 500ms ease-out;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
}

@keyframes fade-notify-out {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

@keyframes fade-notify-in {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

#notify-bottom-right {
	right: 0;
	bottom: 0;
}

#notify-bottom-left {
	left: 0;
	bottom: 0;
}

#notify-top-right {
	right: 0;
}

#notify-top-left {
	left: 0;
}

#notify-bottom-right,
#notify-bottom-left,
#notify-top-right,
#notify-top-left {
	position: fixed;
	z-index: 2000;
}

#notify-top-left,
#notify-top-right {
	top: 0;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}
